.concierge-page {
    display: flex;
    justify-content: space-between;
    padding: 50px;
    color: #ffffff;
    background-color: #0d1b2a;
  }
  
  .left-section {
    width: 45%;
  }
  
  .left-section h1 {
    font-family: Neue Haas Unica;
font-size: 80px;
font-weight: 400;
line-height: 68px;
text-align: left;
text-underline-position: from-font;
text-decoration-skip-ink: none;

  }
  
  .left-section p {
    font-family: Microsoft JhengHei;
font-size: 18px;
font-weight: 290;
line-height: 23.94px;
text-align: left;
text-underline-position: from-font;
text-decoration-skip-ink: none;

  }
  
  .icon-section {
    display: flex;
    gap: 20px;
    padding-top: 20px;
  }
  
  
  .right-section {
    width: 50%;
    display: grid;
    grid-template-areas: 
      "large-image large-image"
      "small-image1 small-image2";
    grid-gap: 15px;
  }
  
  .large-image {
    grid-area: large-image;
  }
  
  .small-image:first-child {
    grid-area: small-image1;
  }
  
  .small-image:last-child {
    grid-area: small-image2;
  }
  
  .image img {
    width: 100%;
    height: 100%;
    border-radius: 8px;
    object-fit: cover;
  }
  